import Headers from '../../header/Headers';
import Footer from '../../footer/Footer';
import s from './Fragment.module.css'
import { Layout, Typography, Divider } from 'antd';
const { Title } = Typography;

const Fragment = () => {
  return (
    <div className="App">
      <Layout className="layout">
        <Headers />
        <div className='content'>
          <Title className="main-title"> </Title>
          <Divider />
          <p className={s.text}><b></b>          Facebook.</p>
          <p className={s.text}>         React.      ,      DOM.</p>
          <div className="content-code">
            <pre><code>{` render() {
    return (
      <React.Fragment>
        <ChildA />
        <ChildB />
        <ChildC />
      </React.Fragment>
    );
  }`}
            </code></pre>

          </div>
          <p className={s.subtitle}> </p>
          <div className="content-code">
            <pre><code>{`class Columns extends React.Component {
  render() {
    return (
      <>
        <td></td>
        <td></td>
      </>
    );
  }
}`}
            </code></pre>

          </div>
          <p className={s.text}>  <code>{` <></> `}</code>  ,     .        .

          </p>
          <p className={s.subtitle}> </p>
          <p className={s.text}>    ,    {`<Fragment />`} .            ,    :</p>
          <div className="content-code">
            <pre><code>{`function Glossary(props) {
  return (
    <dl>
      {props.items.map(item => (
        <Fragment key={item.id}>
          <dt>{item.term}</dt>
          <dd>{item.description}</dd>
        </Fragment>
      ))}
    </dl>
  );
}`}
            </code></pre>

          </div>
          <p className={s.text}> <span className={s.mark}>key</span>   ,    <span className={s.mark}>Fragment</span>.        ,    .

          </p>

          <p className={s.link}><a href='https://ru.reactjs.org/docs/fragments.html' target="_blank" rel='noopener'> </a></p>
          <Divider />
        </div>
        <Footer />
      </Layout >
    </div >)
}

export default Fragment;