import Headers from '../../header/Headers';
import Footer from '../../footer/Footer';
import s from './Events.module.css'
import { Layout, Typography, Divider } from 'antd';
const { Title } = Typography;

const Events = () => {
  return (
    <div className="App">
      <Layout className="layout">
        <Headers />
        <div className='content'>
          <Title className="main-title"></Title>
          <Divider />
          <p className={s.text}>   HTML DOM, React       .</p>
          <p className={s.text}>React    ,   HTML: , ,    . .</p>
          <p className={s.subtitle}> </p>
          <p className={s.text}> React    camelCase:</p>
          <p className={s.text}><code><b>onClick</b></code>  <code><b>onclick</b></code></p>
          <p className={s.text}>  React    :</p>
          <p className={s.text}>{`onClick={shoot}   onClick="shoot()"`}.</p>
          <p className={s.subtitle}> </p>
          <p className={s.text}>    ,    .</p>
          <div className="content-code">
            <pre><code>{`function Football() {
  const shoot = (a) => {
    alert(a);
  }
  return (
    <button onClick={() => shoot("Goal!")}>Take the shot!</button>
  );
}

ReactDOM.render(<Football />, document.getElementById('root'));`}</code></pre>
          </div>
          <p className={s.subtitle}>   </p>
          <p className={s.text}>      React,  .</p>
          <div className="content-code">
            <pre><code>{`function Football() {
  const shoot = (a, b) => {
    alert(b.type);
  }
  return (
    <button onClick={(event) => shoot("Goal!", event)}>Take the shot!</button>
  );
}

ReactDOM.render(<Football />, document.getElementById('root'));`}</code></pre>
          </div>

          <p className={s.link}><a href='https://ru.reactjs.org/docs/handling-events.html' target="_blank" rel='noopener'> </a></p>
          <Divider />
        </div>
        <Footer />
      </Layout>
    </div>)
}

export default Events;