import Headers from '../../header/Headers';
import Footer from '../../footer/Footer';
import s from './Async.module.css'
import { Layout, Typography, Divider } from 'antd';
const { Title } = Typography;

const Async = () => {
  return (
    <div className="App">
      <Layout className="layout">
        <Headers />
        <div className='content'>
          <Title className="main-title">  </Title>
          <Divider />
          <p className={s.text}> -   , ,  ,     ().        AJAX,      . ,   ,   ,      . </p>
          <p className={s.text}>       window.fetch   AJAX-,  Axios  jQuery AJAX.</p>
          <p className={s.text}>       componentDidMount</p>
          <p className={s.subtitle}>:    AJAX-</p>
          <p className={s.text}>  ,   <b>componentDidMount</b>      AJAX-.</p>
          <div className="content-code">
            <pre><code>{`class MyComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: []
    };
  }

  componentDidMount() {
    fetch("https://api.example.com/items")
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({
            isLoaded: true,
            items: result.items
          });
        },
        // :     ,     catch(),
        //         .
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      )
  }

  render() {
    const { error, isLoaded, items } = this.state;
    if (error) {
      return <div>: {error.message}</div>;
    } else if (!isLoaded) {
      return <div>...</div>;
    } else {
      return (
        <ul>
          {items.map(item => (
            <li key={item.id}>
              {item.name} {item.price}
            </li>
          ))}
        </ul>
      );
    }
  }
}`}
            </code></pre>

          </div>

          <p className={s.text}>  :</p>
          <div className="content-code">
            <pre><code>{`function MyComponent() {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [items, setItems] = useState([]);

  // :    [] , 
  //  useEffect    
  //  componentDidMount()
  useEffect(() => {
    fetch("https://api.example.com/items")
      .then(res => res.json())
      .then(
        (result) => {
          setIsLoaded(true);
          setItems(result);
        },
        // :     ,     catch(),
        //         .
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )
  }, [])

  if (error) {
    return <div>: {error.message}</div>;
  } else if (!isLoaded) {
    return <div>...</div>;
  } else {
    return (
      <ul>
        {items.map(item => (
          <li key={item.id}>
            {item.name} {item.price}
          </li>
        ))}
      </ul>
    );
  }
}
`}
            </code></pre>

          </div>

          <p className={s.link}><a href='https://max-frontend.gitbook.io/react-course-ru-v2/asinhronnii-zapros' target="_blank"> </a></p>
          <Divider />
        </div>
        <Footer />
      </Layout>
    </div>)
}

export default Async;