import Headers from '../../header/Headers'
import { Layout, Divider, Typography } from 'antd';
import Footer from '../../footer/Footer';
import s from './Props.module.css'
const { Content } = Layout;
const { Title, Text } = Typography;

const Props = () => {
   return (
      <div className="App">
         <Layout className="layout">
            <Headers />
            <div className='content'>
               <Title className="main-title"> </Title>
               <Divider />
               <p className={s.text}><b></b> -    React-,      .</p>
               <p className={s.text}>    .        !</p>

               <div className="content-code">
                  <pre><code>{`// !
props.number = 42;`}
                  </code></pre>
               </div>
               <p className={s.bold}>props.children</p>
               <p className={s.text}>    props.children.        . :</p>
               <div className="content-code">
                  <pre><code>{`<Welcome>, !</Welcome>`}</code></pre>
               </div>
               <p className={s.text}> , !   props.children   Welcome:</p>

               <div className="content-code">
                  <pre><code>{`function Welcome(props) {
  return <p>{props.children}</p>;
}`}</code></pre></div>
               <p className={s.text}>    this.props.children:</p>

               <div className="content-code">
                  <pre><code>{`class Welcome extends React.Component {
  render() {
    return <p>{this.props.children}</p>;
  }
}`}</code></pre></div>
               <p className={s.link}><a href='https://ru.reactjs.org/docs/components-and-props.html#function-and-class-components' target="_blank" rel='noopener'> </a></p>
               <Divider />
            </div>
            <Footer />
         </Layout>
      </div>)
}

export default Props;