import Headers from '../../header/Headers';
import Footer from '../../footer/Footer';
import s from './Antdesing.module.css'
import { Layout, Typography, Divider } from 'antd';
const { Title} = Typography;

const Antdesing = () => {
  return (
    <div className="App">
      <Layout className="layout">
        <Headers />
        <div className='content'>
          <Title className="main-title">Ant Desing</Title>
          <Divider />
          <p className={s.text}><b>Ant Design</b>-  <span className={s.mark}></span>    React.</p>

          <div className="content-code">
            <pre><code>{`npm install antd --save //   antd`}
            </code></pre>
          </div>
          <p className={s.text}>  antd css  App.css.</p>

          <div className="content-code">
            <pre><code>{`import 'import antd/dist/antd.css' //   App.css   css antd  `}</code></pre>
          </div>
          <p className={s.text}> ,   React       antd.</p>
          <p className={s.subtitle}> Ant Desing</p>
          <ol>
            <li>   ,   -.</li>
            <li>    React  .</li>
            <li>  TypeScript    .</li>
            <li>      .</li>
            <li>    .</li>
            <li>     .</li>
          </ol>


          <p className={s.link}><a href='https://ant.design/' target="_blank"> </a></p>
          <Divider />
        </div>
        <Footer />
      </Layout>
    </div>)
}

export default Antdesing;