import Headers from '../../header/Headers';
import Footer from '../../footer/Footer';
import s from './Form.module.css'
import { Layout, Typography, Divider } from 'antd';
const { Title } = Typography;

const Form = () => {
  return (
    <div className="App">
      <Layout className="layout">
        <Headers />
        <div className='content'>
          <Title className="main-title">  </Title>
          <Divider />
          <p className={s.text}> React       ,    html,       . ,      React</p>
          <div className="content-code">
            <pre><code>{` class UserForm extends React.Component {
                constructor(props) {
                  super(props);
                  this.state = {name: ""};
                    this.onChange = this.onChange.bind(this);
                    this.handleSubmit = this.handleSubmit.bind(this);
                }
              
                onChange(e) {
                  var val = e.target.value;
                  this.setState({name: val});
                }
              
                handleSubmit(e) {
                  e.preventDefault();
                  alert(": " + this.state.name);
                }
              
                render() {
                  return (
                    <form onSubmit={this.handleSubmit}>
                      <p>
                        <label>:</label><br />
                        <input type="text" value={this.state.name} onChange={this.onChange}/>
                      </p>
                      <input type="submit" value="" />
                    </form>
                  );
                }
              }
              
              ReactDOM.render(
                <UserForm />,
                document.getElementById("app")
            )`}</code></pre>
          </div>

          <p className={s.text}>   ,     state:</p>
          <div className="content-code">
            <pre><code>{`  this.state = {name: ""};`}</code></pre>
          </div>
          <p className={s.text}>           state:</p>
          <div className="content-code">
            <pre><code>{` <input type="text" value={this.state.name} onChange={this.onChange} />`}</code></pre>
          </div>
          <p className={s.text}>,         this.state.name.</p>
          <p className={s.text}>            change    onChange.         .        ,         .</p>

          <p className={s.text}>        this.state:</p>
          <div className="content-code">
            <pre><code>{`onChange(e) {
                var val = e.target.value;
                this.setState({name: val});
            }`}</code></pre>
          </div>
          <p className={s.text}>  e.target.value   .     this.state.name    .</p>
          <p className={s.link}><a href='https://ru.reactjs.org/docs/forms.html' target="_blank" rel='noopener'> </a></p>
          <Divider />
        </div>
        <Footer />
      </Layout>
    </div>)
}

export default Form;