import Headers from '../../header/Headers'
import Footer from '../../footer/Footer';
import { Layout, Divider, Typography } from 'antd';
import s from './State.module.css'
const { Title, Text } = Typography;

const State = () => {
   return (
      <div className='App'>

         <Layout className="layout">
            <Headers />
            <div className='content'>
               <Title className="main-title"></Title>
               <Divider />
               <Title level={3}>   ?</Title>
               <p className={s.text}>State   ,  <Text italic> -  </Text>. ,     .</p>
               <Title level={3}>  ?</Title>
               <p className={s.text}>    <b>setState</b></p>
               <div className="content-code">
                  <pre><code>{`setState(updater, [callback])`}
                  </code></pre>

               </div>
               <Title level={3}>  setState?</Title>
               <p className={s.text}> <b>setState()</b>     (state) .   ,   .
               </p>
               <p className={s.text}>   setState     .</p>
               <Title level={3}>    <Text underline></Text> ?</Title>
               <ol>
                  <li>  .</li>
                  <li>  <code>{`state ={ count : 0 }`}</code></li>
                  <li>    <code>hadlerState</code>,    .</li>
                  <li>   , , onClick.</li>
               </ol>
               <div className="content-code">
                  <pre><code>{` class Components extends React.Component {
   state = {
      count: 0
   }

   hadlerState = () => {
      this.setState((state)=>({count:state.count+1}))
   }

   render() {
      return (
         <div>
            <p>{this.state.count}</p>
            <button onClick ={()=> this.hadlerState()}>Click me</button>
         </div>
      )
   }
}`}
                  </code></pre>

               </div>
               <Title level={3}>    <Text underline></Text> ?</Title>

               <ol>
                  <li>  ,    .</li>
                  <li>    useState:<code>{` import {useState} from 'react'`}</code></li>
                  <li>   useState,    .</li>
                  <li>   ,     setState.</li>
               </ol>

               <div className="content-code">
                  <pre><code>{`import {useState} from 'react'

const Components = () => {
   const [count, setCount] = useState();
   return (
      <div>
         <p>{count +1}</p>
         <button onClick={()=>setCount(count+1)}>Click me</button>
      </div>
   )
}`}
                  </code></pre>

               </div>
               <p className={s.link}><a href='https://ru.reactjs.org/docs/faq-state.html' target="_blank" rel='noopener'> </a></p>
               <Divider />
               <Footer />
            </div>
         </Layout >

      </div >)
}

export default State;