import Headers from '../../header/Headers';
import Footer from '../../footer/Footer';
import s from './Useeffect.module.css'
import { Layout, Typography, Divider } from 'antd';
const { Title } = Typography;

const Useeffect = () => {
  return (
    <div className="App">
      <Layout className="layout">
        <Headers />
        <div className='content'>
          <Title className="main-title">useEffect </Title>
          <Divider />
          <p className={s.text}> <b>useEffect</b>          (componentDidMount, componentDidUpdate,  componentWillUnmount).</p>
          <div className="content-code">
            <pre><code>{`import {useEffect} from 'react';`}</code></pre></div>
          <ul>
            <li>UseEffect      ,    - [ ] ( componentDidMount).</li>
            <div className="content-code">
              <pre><code>{` useEffect ( () => { }, [])`}</code></pre></div>
            <li>UseEffect      ,    -    ( ) ( componentDidUpdate).</li>
            <div className="content-code">
              <pre><code>{`useEffect ( () => { }, [name])`}</code></pre></div>
            <li>UseEffect      ,   -  ( componentDidMount + componentDidUpdate).</li>
            <div className="content-code">
              <pre><code>{`  useEffect ( () => { } )`}</code></pre></div>
            <li>UseEffect      ,     ( componentWillUnmount).</li>
            <div className="content-code">
              <pre><code>{`   useEffect ( () => {
 return () => {};
      })`}</code></pre></div>
          </ul>

          <p className={s.link}><a href='https://ru.reactjs.org/docs/hooks-effect.html' target="_blank" rel='noopener'> </a></p>
          <Divider />
        </div>
        <Footer />
      </Layout>
    </div>)
}

export default Useeffect;