import Headers from '../../header/Headers';
import Footer from '../../footer/Footer';
import s from './Virtualdom.module.css'
import { Layout, Typography, Divider } from 'antd';
const { Title } = Typography;

const Virtualdom = () => {
  return (
    <div className="App">
      <Layout className="layout">
        <Headers />
        <div className='content'>
          <Title className="main-title"> DOM</Title>
          <Divider />
          <p className={s.text}><b> DOM (VDOM)</b>    ,                DOM   ,   ReactDOM.    .</p>
          <img src='https://i.postimg.cc/FRqVCqzY/eqp1ffv7pfwux34or4iytxpumli.png' className={s.image}></img>
          <p className={s.text}>DOM   <b>Document Object Model</b> (  ).  , DOM      (user interface, UI)  .    UI, DOM      .    DOM    .</p>
          <p className={s.link}><a href='https://ru.reactjs.org/docs/dom-elements.html' target="_blank" rel='noopener'> </a></p>
          <Divider />
        </div>
        <Footer />
      </Layout>
    </div>)
}

export default Virtualdom;