import Headers from '../../header/Headers';
import Footer from '../../footer/Footer';
import s from './Components.module.css'
import { Layout, Typography, Divider } from 'antd';
const { Title, Text } = Typography;

const Components = () => {
   return (
      <div className="App">
         <Layout className="layout">
            <Headers />
            <div className='content'>
               <Title className="main-title"> </Title>
               <Divider />
               <p className={s.text}><b>React-</b>   ,    ,   React-    .   React-     JavaScript,    React</p>
               <p className={s.subtitle}> : <Text mark></Text>   <Text mark></Text></p>
               <p className={s.text}>    ,  'Components'</p>
               <div className="content-code">
                  <pre><code>{`function Car() {
  return <h2>Hi, I am a Car!</h2>;
}

export default Car;`}
                  </code></pre>
               </div>
               <div className="content-code">
                  <pre><code>{`class Car extends React.Component {
  render() {
    return <h2>Hi, I am a Car!</h2>;
  }
}`}</code></pre>
               </div>

               <p className={s.text}>,    JavaScript-.     ( props)   React-,   ,     .</p>
               <p className={s.link}><a href='https://ru.reactjs.org/docs/components-and-props.html#function-and-class-components' target="_blank"> </a></p>
               <Divider />
            </div>
            <Footer />
         </Layout>
      </div>)
}

export default Components;