import Headers from '../../header/Headers';
import Footer from '../../footer/Footer';
import s from './Refs.module.css'
import { Layout, Typography, Divider } from 'antd';
const { Title, Text } = Typography;

const Refs = () => {
  return (
    <div className="App">
      <Layout className="layout">
        <Headers />
        <div className='content'>
          <Title className="main-title"></Title>
          <Divider />
          <p className={s.text}>React   ,      .  <span className={s.mark}>ref</span>   ,     <code><b>React.createRef()</b></code>  ,    ( API).     ref  ,   DOM-,    ,    (    )   .         DOM   .</p>

          <Title level={3}>    ?</Title>
          <p className={s.text}><i> . ,     .</i>
          </p>
          <p className={s.text}>
             :
          </p>
          <ol>
            <li> ,  .</li>
            <li>.</li>
            <li>  DOM .</li>
          </ol>
          <Title level={3}> </Title>

          <p className={s.text}>     <b>React.createRef()</b>     React    ref.  ,     ,        ,         .</p>
          <div className="content-code">
            <pre><code>{` class MyComponent extends React.Component {
    constructor(props) {
      super(props);
      this.myRef = React.createRef();
    }
    render() {
      return <div ref={this.myRef} />;
    }
  }`}
            </code></pre>

          </div>
          <Title level={3}>  </Title>
          <p className={s.text}> ref     render(),        current.</p>
          <div className="content-code">
            <pre><code>{` const node = this.myRef.current;`}
            </code></pre>

          </div>

          <Title level={3}>  ref  -</Title>
          <div className="content-code">
            <pre><code>{`  class MyComponent extends React.Component {
                constructor(props) {
                  super(props);
                  this.myRef = React.createRef();
                }
                render() {
                  return <div ref={this.myRef} />;
                }
            }
  `}
            </code></pre>

          </div>

          <Title level={3}>     useRef:</Title>
          <p className={s.text}>   ref  -,      .</p>
          <div className="content-code">
            <pre><code>{` import { useRef } from 'react';

function MyComponent () {
    const refContainer = useRef(initialValue);
    return (
        <div ref={refContainer} />
    );
}`}
            </code></pre>

          </div>

          <p className={s.text}>useRef   ref-,  .current     (initialValue).          .</p>
          <p className={s.text}>  ,    .</p>
          <p className={s.link}><a href='https://ru.reactjs.org/docs/refs-and-the-dom.html' target="_blank" rel='noopener'> </a></p>
          <Divider />
        </div>
        <Footer />
      </Layout>
    </div>)
}

export default Refs;